.news {
   width: 100%;
   padding-bottom: 80px;

   .wrapper {
      max-width: 72rem;
      padding: 0 2rem;
      margin: 0 auto;
   }
}

.title {
   h2 {
      font-size: 1.6rem;
      text-transform: uppercase;
      margin-bottom: 2rem;

      span {
         color: #ff6600;
      }
   }
}

.newsList {
   display: grid;
   gap: 1rem;
   grid-template-columns: repeat(1, minmax(0, 1fr));

   .newsItem:nth-child(1) {
      grid-column-start: 1;

      .img {
         height: 100%;

         @media (min-width: 620px) {
            height: 380px;
         }

         @media (min-width: 840px) {
            height: 100%;
         }
      }

      @media (min-width: 500px) {
         grid-column: span 2 / span 2;
      }
   }

   @media (min-width: 500px) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
   }

   @media (min-width: 840px) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
   }
}

.newsItem {
   .header {
      border-radius: 0.25rem;
      overflow: hidden;
   }

   .body {
      padding: 1rem 0.5rem 0.5rem;

      h4 {
         font-size: 1rem;
         font-weight: 700;
         line-height: 1.25;
         color: #292929;
         display: -webkit-box;
         -webkit-line-clamp: 2;
         -webkit-box-orient: vertical;
         overflow: hidden;
         text-overflow: ellipsis;

         transition: color 0.3s ease;

         cursor: pointer;

         &:hover {
            color: #ff6600;
         }
      }

      span {
         font-size: 0.6875rem;
         color: #ff6600;
      }

      p {
         font-size: 0.8125rem;
         color: rgb(115, 115, 115);
         margin-top: 0.375rem;
         display: -webkit-box;
         -webkit-line-clamp: 3;
         -webkit-box-orient: vertical;
         overflow: hidden;
         text-overflow: ellipsis;
      }
   }

   .footer {
      padding-left: 0.5rem;
      padding-right: 0.5rem;
   }

   .newsIcon {
      display: flex;
      align-items: center;
      gap: 1rem;
      font-size: 0.6875rem;

      & > div {
         color: #ff6600;
         display: flex;
         align-items: center;
      }

      span {
         color: #292929;
      }

      svg {
         font-size: 0.9375rem;
         margin-right: 0.25rem;
      }
   }

   .img {
      img {
         display: block;
         width: 100%;
         height: 100%;
      }
   }
}

.newsItem:nth-child(2),
.newsItem:nth-child(3) {
   h4 {
      font-size: 1rem;
   }
}

.showMore {
   display: flex;
   align-items: center;
   color: #ff6600;
   margin-top: 2rem;

   cursor: pointer;

   span {
      margin-left: 0.7rem;
      margin-right: 0.3rem;
      text-transform: capitalize;
   }

   svg {
      animation: goDown 0.9s infinite linear;
   }
}

@keyframes goDown {
   0%,
   100% {
      transform: translateY(-15%);
      animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
   }

   50% {
      transform: none;
      animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
   }
}
